<template>
  <div class="v-header">
    <header>
      <p v-text="bqTitle"></p>
      <time>
        <p class="headtime">{{currentTime}}</p>
      </time>
    </header>
    <div class="headContain">
      <div class="headContain-left">
        <div class="leftNum">
          <div class="leftNum-top"
                      v-text="getSingleHeaderData(0).count"
                      @click="handleClickHandleData(getSingleHeaderData(0))"
        ></div>
          <div class="leftNum-bot" v-text="getSingleHeaderData(0).name"></div>
        </div>
        <div class="rightNum">
          <img src="../assets/img/1icon.svg">
          <div class="rightNum-top"
               v-text="getSingleHeaderData(1).count"
               @click="handleClickHandleData(getSingleHeaderData(1))"
          ></div>
          <div class="rightNum-bot" v-text="getSingleHeaderData(1).name"></div>
        </div>
      </div>
      <div class="headContain-center">
        <template v-for="i in 6">
          <ul>
            <template v-for="headerData in getHeaderData(i * 2 + 1 - 1, i * 2 + 1 - 1 + 2)">

              <li class="mdui-ripple mdui-ripple-blue"
                  @click="handleClickHandleData(headerData)"
                  :class="{ focus : currentHeaderData === headerData}"
              >
                <span class="mdui-count" v-text="headerData.count"></span>
                <span class="mdui-title" v-text="headerData.name"></span>
              </li>

            </template>
          </ul>

        </template>
      </div>
      <div class="headContain-right">
        <div class="leftNum">
          <img src="../assets/img/2icon.svg">
          <div class="leftNum-top"
               v-text="getSingleHeaderData(14).count"
               @click="handleClickHandleData(getSingleHeaderData(14))"
          ></div>
          <div class="leftNum-bot" v-text="getSingleHeaderData(14).name"></div>
        </div>
        <div class="rightNum">
          <template v-for="i in 6">

            <ul>
              <template v-for="headerData in getHeaderData(i * 2 + 14 - 1, i * 2 + 14 - 1 + 2)">

                <li class="mdui-ripple mdui-ripple-blue"
                    @click="handleClickHandleData(headerData)"
                    :class="{ focus : currentHeaderData === headerData}"
                >
                  <span class="mdui-count" v-text="headerData.count"></span>
                  <span class="mdui-title" v-text="headerData.name"></span>
                </li>

              </template>
            </ul>

          </template>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
  import moment from 'moment'
  import {mapState} from 'vuex'

  export default {
    props: {
      bqTitle: {
        type: String,
        required: true
      },
      headerData: {
        type: Array,
        required: true
      }
    },
    data() {
      return {
        currentTime: null,
        titleStatus: false
      }
    },
    beforeCreate() {
    },
    created() {
      this.setCurrentTime();
      setInterval(this.setCurrentTime, 1000);
    },
    methods: {
      /*
       * 时间format函数
       * @author jasmine
       * @return {Number}    时间format的结果
       * */
      setCurrentTime() {
        this.currentTime = moment().format('YYYY-MM-DD HH:mm:ss');
      },
      /*
       * 数组截取函数
       * @author jasmine
       * @param begin {Number} 数组开始位置的值
       * @param end {Number} 数组结束位置的值
       * @return {Array}    截取的结果
       * */
      getHeaderData(begin, end) {
        return this.headerData.slice(begin, end);
      },
      /*
      * 数组截取单个对象函数
      * @author jasmine
      * @param index {Number} 数组开始位置的值
      * @return {Object}    截取的结果
      * */
      getSingleHeaderData(index) {
        let headerData = this.getHeaderData(index, index + 1).shift();
        return headerData ? headerData : {};
      },
      /*
      * 当前头部点击数据提交vuex
      * @author jasmine
      * @param headerData {Number} 数组开始位置的值
      * */
      handleClickHandleData(headerData) {
        if (this.currentHeaderData === headerData) {
          this.$store.commit('settingCurrentHeaderData', null);
        } else {
          this.$store.commit('settingCurrentHeaderData', headerData);
        }
      },
    },
    computed: {
      ...mapState([
        'currentHeaderData',
      ])
    },
    watch: {
      currentHeaderData: function (value, oldValue) {
//        console.log(value, 'Changed');
      }
    },
    filters: {},
    components: {}
  }
</script>
<style lang="less" scoped>
  .line {
    height: 110px;
    width: 2px;
    color: #cccccc;
  }

  .focus .mdui-title, .focus .mdui-count {
    color: #1358EB;
  }

  .mdui-ripple {
    width: 50%;
    margin: 0 auto;
    border-radius: 20px;
  }

  .headContain-right .mdui-ripple {
    width: 80% !important;
    height: 54px !important;
  }
  .headContain-right .rightNum ul> .mdui-ripple:nth-child(2){
    margin-top: 10px;
  }
  header {
    width: 100%;
    height: 50px;
    background-color: #1747cf;
  }

  header p {
    font-size: 22px;
    color: #fff;
    line-height: 50px;
    text-align: center;
  }

  .headtime {
    position: absolute;
    right: 60px;
    line-height: 50px;
    top: 0;
  }

  .headContain {
    width: 1920px;
    height: 140px;
    margin-top: 7px;
  }

  .headContain-left {
    position: relative;
    float: left;
    width: 383px;
    height: 138px;
    background: #FFFFFF;
    margin-left: 10px;
    border: 1px solid #E5E7E9;
    box-shadow: 0 0 5px #99999912;
  }

  .headContain-left .leftNum {
    width: 50%;
    height: 100%;
    background: #FFFFFF;
    position: relative;
    float: left;
    background: url("../assets/img/line.png") no-repeat right;
  }

  .headContain-left .rightNum {
    width: 50%;
    height: 100%;
    background: #FFFFFF;
    position: relative;
    float: right;
  }

  .headContain-left .leftNum-top {
    min-width: 88px;
    text-align: center;
    font-size:55px ;
    font-family:"futura md bt";
    color: #1463ef;
    position: absolute;
    top: 15px;
    left: 57px;
  }

  .headContain-left .leftNum-bot {
    min-width: 88px;
    position: absolute;
    text-align: center;
    top: 92px;
    left: 58px;
    font-size: 14px;
    color: #333333;
  }

  .headContain-left .rightNum img {
    position: absolute;
    top: 54px;
    right: 124.3px;
    width: 35px;
  }

  .headContain-left .rightNum-top {
    min-width: 88px;
    text-align: center;
    font: 48px "futura md bt";
    color: #1463ef;
    position: absolute;
    top: 25px;
    right: 35px;
  }

  .headContain-left .rightNum-bot {
    min-width: 88px;
    text-align: center;
    position: absolute;
    top: 87px;
    right: 35px;
    font-size: 14px;
    color: #666666;
  }

  .headContain-center {
    float: left;
    position: relative;
    width: 863px;
    height: 108px;
    background: #FFFFFF;
    margin-left: 10px;
    border: 1px solid #E5E7E9;
    box-shadow: 0 0 5px #99999912;
  }

  .headContain-center {
    padding: 10px 40px 20px;
  }

  .headContain-right .rightNum ul {
    float: left;
    width: 100%;
    height: 100%;
  }

  .headContain-right .rightNum ul span {
    display: block;
  }

  .headContain-center ul {
    float: left;
    width: 16.6%;
    height: 100%;
    background: url("../assets/img/line.png") no-repeat right;
  }

  .headContain-center ul > span {
    display: block;
  }

  .headContain-center ul:nth-child(6) {
    background: none;
  }

  .headContain-center ul > li:nth-child(2) {
    margin-top: 10px;
  }

  .headContain-center ul > li {
    text-align: center;
    height: 50%;
  }

  .headContain-right {
    float: left;
    position: relative;
    width: 548px;
    height: 138px;
    background: #FFFFFF;
    margin-left: 10px;
    border: 1px solid #E5E7E9;
    box-shadow: 0 0 5px #99999912;
  }

  .headContain-right img {
    width: 35px;
    position: absolute;
    top: 54px;
    left: 36px;
  }

  .headContain-right .leftNum {
    width: 208px;
    height: 140px;
    float: left;
    background: url("../assets/img/line.png") no-repeat right;
  }

  .headContain-right .leftNum .leftNum-top {
    min-width: 88px;
    text-align: center;
    font-size: 48px;
    color: #1463ef;
    position: absolute;
    left: 80px;
    top: 25px;
  }

  .headContain-right .leftNum .leftNum-bot {
    min-width: 88px;
    text-align: center;
    font-size: 14px;
    color: #666666;
    position: absolute;
    left: 80px;
    top: 87px;
  }

  .headContain-right .rightNum {
    width: 338px;
    height: 140px;
    float: left;
    position: relative;
  }

  .headContain-right .rightNum ul:nth-child(1) {
    margin-left: 60px;
  }

  .headContain-right .rightNum ul:nth-child(2) {
  }

  .headContain-right .rightNum ul {
    float: left;
    width: 25%;
    height: 100%;
  }

  .headContain-right .rightNum ul li:nth-child(1) {
    margin-top: 10px;
  }

  .headContain-right ul > li {
    text-align: center;
    height: 54px;
  }

  .headContain-center span {
    display: block;
  }

  .mdui-count {
    height: 36px;
    line-height: 36px;
    font-size: 36px;
    color: #333333;
    font-family:"FuturaBT-Medium";
    //font-family: "FuturaBT-Medium";
  }

  .mdui-title {
    height: 13px;
    line-height: 13px;
    font-size: 13px;
    color: #666666;
    font-family: "Microsoft YaHei";
    margin-top: 5px;
  }
</style>



