/**
* @author wyh
* @change jasmine 2017/12/04*
* @date 2017/11/27
* @module listHome
*/
<template>
  <div>
    <div class="header">
      <v-header :bq-title = "BQTitle"
                :header-data = "headerData"
      ></v-header>
    </div>
    <div class="content">
      <template v-for = "(item, index) in FZInfos">
        <div class = "card">
          <v-card :card-content-style = "getCardContentStyle(index)"
                  :title = "item.FZName"
                  :rows = "item.RWinfos"
          ></v-card>
        </div>
      </template>
    </div>
    <div class = "footer">
      <v-footer></v-footer>
    </div>
    <v-info class = "animated"
            :class = "toggleInfo()"
            :patientInformation = "patientInformation"
    ></v-info>
  </div>
</template>

<script>
  import vHeader from '@/components/Header.vue'
  import vFooter from '@/components/Footer.vue'
  import vCard from '@/components/Card.vue'
  import vInfo from '@/components/Info.vue'
  import {mapGetters} from 'vuex'
  import {mapState} from 'vuex'

  export default {
    data() {
      return {
        cardContentStyle: {height: '329px'},
        isShowMask: false
      };
    },
    computed: {
      ...mapGetters({
        BQTitle: 'listHome/BQTitle',//病区名称
        headerData: 'listHome/headerData',//总标题信息数据
        FZInfos: 'listHome/FZInfos',//卡片信息数据
        currentState: 'currentState',//卡片标签当前数据
        historyState: 'historyState'//卡片标签访问历史记录
      }),
      ...mapState('listHome', {
        patientInformation : 'patientInformation',
      })
    },
    watch: {
      currentState: function (value, oldValue) {
        if (value || oldValue == false) {
          this.$store.dispatch('listHome/fetchPatientInformation')
        }
      }
    },
    methods: {
      /*
      * 对子组件不同样式的控制
      * @author jasmine
      * @param index {Number} 卡片index
      * @return {Object}  卡片返回的样式
      * */
      getCardContentStyle(index) {
        let height;
        if (index < 4) {
          height = '329px';
        } else {
          height = '404px';
        }
        return {height};
      },
      /*
      * 对右边病人信息卡片的控制
      * @author jasmine
      * @return {String} class名
      * */
      toggleInfo() {
        if (!!this.currentState) {
          return 'bounceInRight'
        }
        if (!!this.currentState == false && !!this.historyState) {
          return 'bounceOutRight'
        }
      }
    },
    beforeCreate() {
      this.$store.dispatch('listHome/fetchListHomeData');
    },
    created() {
    },
    mounted() {
    },
    components: {
      vHeader,
      vFooter,
      vCard,
      vInfo
    }
  }
</script>

<style lang="less" scoped>
  .opacity {
    opacity: 0;
  }

  .header {
    width: 1920px;
    height: 197px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }

  .content {
    width: 1900px;
    height: 842px;
    position: absolute;
    top: 204px;
    left: 10px;
    overflow: hidden;
  }

  .card {
    float: left;
    background: #FFFFFF;
    margin-right: 10px;
    box-shadow: 0 0 5px #99999912;
    border: 1px solid #E5E7E9;
    margin-right: 10px;
  }

  .card:nth-child(4n) {
    margin-right: 0 !important;
  }

  .card:nth-child(5),
  .card:nth-child(6),
  .card:nth-child(7),
  .card:nth-child(8) {
    margin-top: 7px !important;
  }
  .card:nth-child(9),
  .card:nth-child(10),
  .card:nth-child(11),
  .card:nth-child(12) {
    margin-top: 7px !important;
  }
  .card:nth-child(1),
  .card:nth-child(4),
  .card:nth-child(5),
  .card:nth-child(8) {
    width: 333px !important;
  }

  .card:nth-child(2),
  .card:nth-child(3),
  .card:nth-child(6),
  .card:nth-child(7) {
    width: 598px !important;
  }

  .card:nth-child(1),
  .card:nth-child(2),
  .card:nth-child(3),
  .card:nth-child(4) {
    height: 376px !important;
  }

  .card:nth-child(5),
  .card:nth-child(6),
  .card:nth-child(7),
  .card:nth-child(8) {
    height: 452px !important;
  }

  .footer {
    width: 1920px;
    height: 30px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
</style>



