<template>
  <div class="cardContainer">
    <div class="cardTitle font-family-Microsoft font-size-21 padding-left-10" v-text="title"></div>
    <div class="cardContent" :style="cardContentStyle" ref="itemContainer">
      <!--<div class="scrollContainer">-->
      <template v-for="(row,index) in rows">
        <ul class="cardRow clear" :class="{ focus : isFocus(row, index) }" :ref="getItemRefName(row.RWPX)">
          <!--:style="getLabelNameStyle(row.RWName)"-->
          <li class="mdui-float-left margin-top-3">{{row.RWName}}</li>
          <span class="mdui-float-left colon margin-top-3">:</span>
          <template v-for="item in row.CWinfos">
            <li class="mdui-float-left"
                @click="settingState(item)"
                :class="{visited: currentState === item}"
            >
              <div v-html="getText(item.CWDM)"
                   :class="getColor(item.STATUS)"
                   class="mdui-ripple"
              ></div>
            </li>
          </template>
        </ul>
      </template>
      <!--</div>-->
    </div>

  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import {mapGetters} from 'vuex'

  export default {
    props: {
      cardContentStyle: {
        type: Object,
        default: () => {
          return {}
        }
      },
      title: {
        type: String,
        required: true
      },
      rows: {
        type: Array,
        required: true
      }
    },
    data() {
      return {
        topIndex: null
      }
    },
    computed: {
      ...mapState([
        'currentHeaderData'
      ]),
      ...mapGetters([
        'currentState'
      ])
    },
    watch: {
      currentHeaderData(value) {
        if (value) {
          this.scrollToItem();
        }
      }
    },
    methods: {
      /*
       * 对卡片标题长度控制
       * @author jasmine
       * @param name {String} 卡片标题值
       * @return {Object}  卡片标题的宽度样式
       * */
      getLabelNameStyle(name) {
        let maxLength;
        let secondLength;
        let lengths = [];
        this.rows.forEach(value => {
          if (value.RWName == 'T.P.R.BP' || value.RWName == 'BP(BID)') {
            return;
          }
          lengths.push(value.RWName.length);
        });
        lengths = lengths.sort((a, b) => {
          return a < b
        }); // 倒序排序
        maxLength = lengths.shift(); // 取第一个，并且删除数组元素，也就是最大那个
        while (lengths.length) { // 如果数组还有值，则循环
          secondLength = lengths.shift(); // 取第一个，并且删除元素
          if (secondLength !== maxLength) { // 防止有两个最大值相同的，如果相同则继续循环，不相同则代表这是第二大的
            break;
          }
        }

        let styleObject = {};
        let fontSize = 18;

        // 如果最大值小于等于4 则宽度为4乘以字体大小
        if (maxLength <= 4) {
          styleObject.width = `${4 * fontSize}px`;
          return styleObject;
        }

        // 如果当前名字等于最大值 则返回最大值乘以字体大小
        if (name.length === maxLength) {
          styleObject.width = `${maxLength * fontSize}px`;
          return styleObject;
        }

        // 如果第二大的长度大于4 则返回第二大长度乘以字体大小
        if (secondLength > 4) {
          styleObject.width = `${secondLength * fontSize}px`;
          return styleObject;
        }

        return styleObject;
      },
      /*
       * 对卡片内标签的背景颜色控制
       * @author jasmine
       * @param status {Number} 状态值
       * @return {String}  标签的class名
       * */
      getColor(status) {
        switch (status) {
          case '1':
            return 'danger';
          case '2':
            return 'ill';
          case '3':
            return 'nursing';
          case '4':
            return 'normal';
        }
      },
      /*
       * 对卡片内标签的汉字进行正则校验，然后返回字体较小的标签
       * @author jasmine
       * @param text {String} 标签内的Text文本
       * @return {String}  字体较小的标签
       * */
      getText(text) {
        return text.replace(/[^\x00-\xff]+/g, '<span class="hanzi" style="font-size:12px;margin-top:7px">$&</span>');
      },
      isFocus(row, index) {
        return this.currentHeaderData && this.currentHeaderData.interrelatedRWPY === row.RWPX
      },
      settingState(item) {
        if (item === this.currentState) {
          this.$store.commit('settingCurrentCWDMState', null)
        } else {
          this.$store.commit('settingCurrentCWDMState', item)
        }
      },
      getItemRefName(RWPX) {
        return 'item-' + RWPX;
      },
      scrollToItem() {
        let rwpx = this.currentHeaderData.interrelatedRWPY;
        let elements = this.$refs[this.getItemRefName(rwpx)];
        if (!elements) {
          return;
        }
        let element = elements[0];
        let itemContainer = this.$refs['itemContainer'];
        itemContainer.scrollTop = element.offsetTop;
      }
    },
    created() {

    },
    mounted() {
    }
  }
</script>

<style scoped>
  .margin-top-3 {
    margin-top: 3px;
  }

  .cardContent {
    overflow-y: auto;
    /*position: relative;*/
    /*overflow: hidden;*/
  }

  .scrollContainer {
    /*position: absolute;*/
  }

  .colon {
    line-height: 34px;
    font-size: 16px;
    margin-right: 6px;
  }

  .cardTitle {
    height: 47px;
    line-height: 47px;
    background: #FFFFFF;
    font-weight: bold;
  }

  .cardRow {
    padding-left: 10px;
    padding-bottom: 3px;
  }

  .cardRow > li:nth-child(1) {
    width: 80px;
    text-align-last: justify;
    font-size: 16px;
    line-height: 34px;
    color: #666666;
  }

  .cardRow > li div {
    width: 45px;
    text-align: center;
    margin: 3px 10px 0 0;
    font-size: 21px;
    font-family: "Microsoft YaHei";
    font-weight: bold;
    border-radius: 15px;
  }

  .cardRow:nth-child(2n-1) {
    background: #f8f9fc;
  }

  .cardRow:nth-child(2n) {
    background: #ffffff;
  }

  .danger {
    color: #DA3333;
    border: 1px solid #efbebe;
    display: flex;
    justify-content: center;
  }

  .ill {
    color: #ff7f00;
    border: 1px solid #FDE3C4;
    display: flex;
    justify-content: center;
  }

  .nursing {
    color: #00a25d;
    border: 1px solid #CAF6E1;
    display: flex;
    justify-content: center;
  }

  .normal {
    color: #1159EB;
    border: 1px solid #DEEFFF;
    display: flex;
    justify-content: center;
  }

  .focus {
    background: #dce6ff !important;
    transition: 1s;
    animation: myfirst 1s;
    -moz-animation: myfirst 1s; /* Firefox */
    -webkit-animation: myfirst 1s; /* Safari and Chrome */
    -o-animation: myfirst 1s;
  }

  @keyframes myfirst
  {
    from, 30%, to {
      animation-timing-function: cubic-bezier(0.215,0.5, 1.000);
    }
    0%   {background:#93bbff;}
    30%  {background: #93bbff;}
    100% {background: #dce6ff;}
  }


  .focus li:nth-child(1) {
    /*color: #FFFFFF !important;*/
    /*transition: .3s;*/
  }

  .focus .colon {
    /*color: #FFFFFF !important;*/
    /*transition: .3s;*/
  }

  .focus .danger,
  .focus .ill,
  .focus .nursing,
  .focus .normal {
    background: #ffffff;
  }

  .visited .danger,
  .visited .ill,
  .visited .nursing,
  .visited .normal {
    transition: .3s;
    color: #ffffff;
  }

  .visited .danger {
    transition: .3s;
    border: 1px solid #D52C2D !important;
    background: #D52C2D;
  }

  .visited .ill {
    transition: .3s;
    border: 1px solid #ff7f00 !important;
    background: #ff7f00;
  }

  .visited .nursing {
    transition: .3s;
    border: 1px solid #00a25d !important;
    background: #00a25d;
  }

  .visited .normal {
    transition: .3s;
    border: 1px solid #1159EB !important;
    background: #1159EB;
  }
</style>



