<template>
  <div class="infoCard">

    <div class="head">
      <div class="top"><p>病人信息</p></div>
      <div class="msg">
        <img src="../assets/img/headshot/男青年.svg" alt="头像">
        <p class="infoName" v-text="information.Name"></p>
        <span class="age">{{information.Age}}岁</span>
        <p v-text="information.CWDM"></p><span class="position">床</span>
      </div>
    </div>
    <div class="contain">
      <div class="part-1">
        <p v-text="information.Title"></p>
        <p v-text="information.Content"></p>
      </div>
      <div class="part-1">
        <p v-text="information.CheckTitle"></p>
        <p v-text="information.CheckTime"></p>
      </div>
      <div class="close" :click="toggleInfo">
        <button class="mdui-ripple mdui-ripple-blue" @click="toggleInfo">关闭</button>
      </div>

    </div>


  </div>
</template>
<script>
  import {mapState} from 'vuex'
  import {mapGetters} from 'vuex'

  export default {
    props: {
      patientInformation: {
        type: Object,
        default: () => {
          return {}
        }
      }
    },
    data() {
      return {};
    },
    computed: {
      ...mapGetters({
        currentState: 'currentState',//卡片标签当前数据
        historyState: 'historyState'//卡片标签访问历史记录
      }),
      information() {
        return this.patientInformation || {};
      }
    },
    watch: {},
    methods: {
      toggleInfo() {
        this.$store.commit('settingCurrentCWDMState', null)
      }
    },

  }

</script>

<style scoped>
  .infoCard {
    width: 346px;
    height: 839px;
    background-color: #ffffff;
    position: absolute;
    bottom: 37px;
    right: -346px;
    -webkit-box-shadow: -5px 5px 5px #448aff40;
    box-shadow: -5px 5px 5px #448aff40;
  }

  .head {
    height: 310px;
    width: 100%;
    background-color: #EFF3F4;
  }
  .infoName{
    padding-top: 10px;
  }
  .top {
    width: 100%;
    height: 180px;
    background: linear-gradient(left, #0081FD, #00B9FE);
    text-align: center;
    border-radius: 2px;
  }

  .top p {
    font-size: 24px;
    color: #fff;
    padding: 20px 0;
  }

  .msg {
    width: 242px;
    height: 221px;
    background: #FFFFFF;
    margin: -107px auto 0 auto;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 20px;
    position: relative;

    text-align: center;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.18)
  }

  .msg img {
    width: 96px;
    border-radius: 48px;
    padding-top: 22px;

  }

  .msg p {
    font-size: 24px;
    color: #1F1F1F;
  }

  .age {
    font-size: 14px;
    color: #7C7C7C;
    position: absolute;
    top: 142px;
    right: 45px;
  }

  .position {
    font-size: 18px;
    color: #1f1f1fe8;
    position: absolute;
    top: 171px;
    right: 77px;
  }

  .contain {
    width: 100%;
    height: 532px;

  }

  .part-1 {
    width: 100%;
    height: 120px;
    background: #ffffff;
    box-sizing: border-box;
    padding: 15px 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);

  }

  .part-1 p:nth-child(1) {
    color: #226DEE;
    font-size: 22px;

  }

  .part-1 p:nth-child(2) {
    color: #464646eb;
    font-size: 18px;
    line-height: 23px;

  }

  .part-1 p:nth-child(2) span {
    color: rgba(0, 0, 0, 0.92);
    margin-left: 10px;
  }

  .close {
    width: 238px;
    margin: 150px auto 0 auto;

  }

  button {
    width: 238px;
    height: 47px;
    background: linear-gradient(left, #0081FD, #00B9FE);
    font-size: 24px;
    color: #fff;
    border-radius: 23px;
    border: none;
    -webkit-box-shadow: 0px 5px 7px #8888882e;
    -moz-box-shadow: 0px 5px 7px #8888882e;
    box-shadow: 0px 5px 7px #8888882e;
    margin: 0 auto;
  }
</style>



